<template>
  <div class="class-report">
    <van-nav-bar title="3iClass Q&A" left-arrow @click-left="back" />
    <div v-if="showNav">
      <van-tree-select height="100vh" @click-nav="clickNav" @click-item="clickItem" :items="items" :main-active-index.sync="active" />
    </div>
    <div v-if="!showNav" style="width:100%">
      <van-panel :title="title">
        <div style="margin:2vh 4vh; font-size: 14px;">
          原因： {{reason}}
        </div>
        <div style="margin:2vh 4vh; font-size: 14px; color: grey">请尝试以下指引操作</div>
        <div style="margin:auto 4vh; font-size: 14px; word-break: break-all;" v-html="content"></div>
        <div v-if="imageSrc" style="text-align: center;">
          <van-image :src="imageSrc" />
        </div>
      </van-panel>
      <div style="margin:2vh 4vh; font-size: 14px; color: grey">如问题未能解决，请联系客服同事处理</div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
export default {
  name: 'CRM',
  components: {
  },
  data () {
    return {
      showNav: true, // 是否显示导航
      content: '',
      title: '',
      reason: '',
      imageSrc: '',
      active: 0,
      items: [
        {
          text: '音视频',
          className: 'my-class',
          children: [
            {
              text: '头像视频卡',
              id: 1,
            },
            {
              text: '头像视频模糊或有马赛克',
              id: 2,
            },
            {
              text: '绿屏、花屏或黑白屏',
              id: 3,
            },
            {
              text: '声音有回声、杂音',
              id: 4,
            },
            {
              text: '看不到视频或听不到声音',
              id: 5,
            },
          ],
        },
        {
          text: 'ppt',
          className: 'my-class',
          children: [
            {
              text: 'ppt打开不了',
              id: 1,
            },
            {
              text: 'ppt某页打不开',
              id: 2,
            },
            {
              text: '上传ppt转码失败',
              id: 3,
            }
          ],
        },
        {
          text: '文件上传',
          className: 'my-class',
          children: [
            {
              text: '上传文件失败',
              id: 1,
            }
          ],
        },
        {
          text: '本地日志上传',
          className: 'my-class',
          children: [
            {
              text: '日志上传',
              id: 1,
            }
          ],
        },
        {
          text: '网络异常',
          className: 'my-class',
          children: [
            {
              text: '登录验证码刷不出来',
              id: 1,
            },
            {
              text: '网络错误，请检查网络',
              id: 2,
            }
          ],
        },
        {
          text: '版本更新',
          className: 'my-class',
          children: [
            {
              text: '下载安装包失败',
              id: 1,
            }
          ],
        }
      ]
    }
  },
  computed: {
  },
  created () {
  },
  mounted () {
  },
  methods: {
    back () {
      this.showNav = true
    },
    clickNav (index) {
      this.active = index
    },
    clickItem (data) {
      this.showNav = false
      this.imageSrc = ''
      switch (this.active) {
        case 0: // 音视频
          switch (data.id) {
            case 1: // 头像视频卡
              this.title = '头像视频卡'
              this.reason = '网络带宽不足、设备性能瓶颈、用户误操作等'
              this.content = '■ 关闭其他无关的应用/软件</br>■ 切换到高质网络</br>■ 开启“不看同学”模式</br>■ 退出应用重新进入</br>'
              this.imageSrc = '/image/cut_video.jpg'
              break;
            case 2: // 头像视频模糊或有马赛克
              this.title = '头像视频模糊或有马赛克'
              this.content = '■ 关闭其他无关的应用/软件</br>■ 切换到高质网络</br>■ 退出应用重新进入</br>'
              break;
            case 3: // 绿屏、花屏或黑白屏
              this.title = '绿屏、花屏或黑白屏'
              this.content = '■ 关闭其他无关的应用/软件</br>■ 切换到高质网络</br>■ 退出应用重新进入</br>'
              break;
            case 4: // 声音有回声、杂音
              this.title = '声音有回声、杂音'
              this.content = '■ 待补充'
              break;
            case 5: // 看不到视频或听不到声音
              this.title = '看不到视频或听不到声音'
              this.content = '■ 关闭其他无关的应用/软件</br>■ 切换到高质网络</br>■ 退出应用重新进入</br>'
              break;
            default:
              break;
          }
          break;
        case 1: // ppt
          switch (data.id) {
            case 1: // ppt打开不了
              this.title = 'ppt打开不了'
              this.reason = '网络异常、PPT格式问题'
              this.content = '■ 检查当前网络是否正常</br>■ 刷新当前PPT</br>■ 刷新白板</br>■ 退出应用重新进入</br>■ 将PPT提交给客服同事'
              this.imageSrc = '/image/cut_video.jpg'
              break;
            case 2: // ppt某页打不开
              this.title = 'ppt某页打不开'
              this.reason = '网络异常、PPT格式问题'
              this.content = '■ 检查当前网络是否正常</br>■ 刷新当前页</br>■ 刷新白板</br>■ 退出应用重新进入</br>■ 将PPT提交给客服同事'
              this.imageSrc = '/image/refresh_ppt.jpg'
              break;
            case 3: // 上传ppt转码失败
              this.title = '上传ppt转码失败'
              this.reason = 'PPT格式问题'
              this.content = '■ 用Power Point 打开ppt，如提示修复点击修复，保存，重新上传</br>■ 将PPT提交给客服同事</br>'
              break;
            default:
              break;
          }
          break;
        case 2: // 文件上传
          switch (data.id) {
            case 1: // 上传文件失败
              this.title = '上传文件失败'
              this.reason = '网络抖动、腾讯云cos异常'
              this.content = '■ 留意页面上传失败的错误提示</br>■ 重新尝试上传</br>'
              break;
            default:
              break;
          }
          break;
        case 3: // 本地日志
          switch (data.id) {
            case 1: // 日志上传
              this.title = '日志上传'
              this.reason = '/'
              this.content = '■ 点击上传日志按钮</br>'
              this.imageSrc = '/image/upload_log.jpg'
              break;
            default:
              break;
          }
          break;
        case 4: // 网络错误提示
          switch (data.id) {
            case 1: // 登录验证码刷不出来
              this.title = '登录验证码刷不出来'
              this.reason = '域名解析异常、DNS服务器异常'
              this.content = '■ 检查当前网络是否正常。如网络有问题，请联系运营商</br>■ 网络正常，但验证码刷不出来，需要修改dns</br>■ windows:  https://zhidao.baidu.com/question/1704903795598756500.html</br>■  mac: https://jingyan.baidu.com/article/86f4a73eab9c6237d6526913.html'
              break;
            case 2: // 网络错误，请检查网络
              this.title = '网络错误，请检查网络'
              this.reason = '域名解析异常、DNS服务器异常、后台服务异常'
              this.content = '■ 检查当前网络是否正常。如网络有问题，请联系运营商</br>■ 网络正常, 但操作一直提示“网络错误，请检查网络”,，需要修改dns</br>■ windows:  https://zhidao.baidu.com/question/1704903795598756500.html</br>■  mac: https://jingyan.baidu.com/article/86f4a73eab9c6237d6526913.html'
              break;
            default:
              break;
          }
          break;
        case 5: // 版本更新
          switch (data.id) {
            case 1: // 下载更新失败
              this.title = '下载更新失败'
              this.reason = '网络抖动、下载文件有误'
              this.content = '■ 检查当前网络是否正常。</br>■ 访问官网下载安装包'
              break;
            default:
              break;
          }
          break;
        default:
          break;
      }
      console.log(data)
    }
  }
}
</script>

<style lang="less">
</style>
